@import url('https://fonts.googleapis.com/css2?family=Gothic+A1&family=Old+Standard+TT&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Old Standard TT';
  font-weight: 400;
}

::-webkit-scrollbar {
  display: none;
}

.orange, .active {
  color: #FFAE2B !important;
}

.white {
  color: #ACA592;
}